$(document).ready(function() {

    // Hover-Click
    $(".item").hover(onIn, onOut);
    $(".item .show").click(function() {
        if ($(this).parents(".item").hasClass("item--active")) {
            $(this).parents(".item").removeClass("item--active")
        } else {
            $(this).parents(".item").addClass("item--active")
        }
    })

    // Delete
    $('.delete').click(function () {
        $(this).parents('.delete-item').addClass('delete-item--active')
    })

})

function onIn() {
    if (window.innerWidth > 320) {
        let el = $(this)
        setTimeout(function() {
            if (el.is(':hover')) {
                console.log(el)
                el.addClass("item--active")

            }
        }, 200);
    }
}

function onOut() {
    if (window.innerWidth > 320) {
        $(this).removeClass("item--active")
    }
}